nav {
  background: rgba(0, 0, 0, 0.3);
  width: max-content;
  display: block;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: calc(50% - 11rem);
  bottom: 2rem;
  display: flex;
  gap: 0.8rem;
  border-radius: 3rem;
  backdrop-filter: blur(15px);
}

nav a {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  color: var(--color-light);
  font-size: 1.1rem;
}

nav a:hover {
  background: rgba(0, 0, 0, 0.3);
}

nav a.active {
  background: var(--color-primary);
  color: var(--color-bg);
}

/* ==================== MEDIA QUERIS (SMALL DEVICES) ====================== */

@media (max-width: 480px) {
  nav {
    padding: 0.5rem 1rem;
    left: 50%;
    bottom: 1rem;
    transform: translateX(-50%);
    gap: 0.5rem;
  }

  nav a {
    padding: 0.7rem;
    font-size: 1rem;
  }
}
